.App {
  background-color: #D5CFE1;
  color: #0D160B;
}

.header {
  min-height: 10vh;
  display: flex;
  flex-direction: row-reverse;
  gap: 0px 20px;
  padding-right: 10px;
  padding-top: 10px;
  background-color: #D5CFE1;
}

.link {
  color: #0D160B;
  text-decoration: none;
  font-size: 1.5em;
  font-weight: bold;
}
.link:hover {
  text-decoration: underline;
  color: #7785FF;
}
.link:active {
  color: #FB5012;
}

.body {
  display: flex;
  flex-direction: column;
  min-height: 80vh;
  padding-left: 10px;
}

.footer {
  display: flex;
  flex-direction: column;
  min-height: 10vh;
  padding-left: 10px;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
